<script>
import 'swiper/dist/css/swiper.css'
import Swiper from 'swiper'
import calendar from '../calendarList/index'
export default{
	data(){
		return {
			scrollTop:'',
			xfIndex:'1',
			count:0,
			layac:false,
			typetext1:'今天',
			typetext2:'明天',
			starTime:'2018-11-16',
			endTime:'2018-11-17',
			amountDay:'1',
			calendarShow:false
		}
	},
	components:{
		calendar
	},
	methods:{
		today(){
			let now = new Date();
            this.year = now.getFullYear()
            this.month = now.getMonth()+1
            this.day = now.getDate()<10?('0'+now.getDate()):now.getDate()
            return this.year+','+this.month+','+this.day
		},
		tommorrow(){
			let now = new Date();
			now.setTime(now.getTime()+24*60*60*1000)
            this.year = now.getFullYear()
            this.month = now.getMonth()+1
            this.day = now.getDate()<10?('0'+now.getDate()):now.getDate()
            return this.year+','+this.month+','+this.day
		},
		myclose(obj){
			console.log(this.scrollTop)
			this.calendarShow = obj.bool
			window.scrollTo(0,this.scrollTop)
		},
		myconfirm(obj){
			this.calendarShow = obj.bool
		},
		myselect(obj){
			console.log(obj)
			this.starTime = obj.starTime
			this.endTime = obj.endTime
			this.amountDay = (new Date(this.endTime) - new Date(this.starTime))/(24*60*60*1000)
			var tstr=this.today().toString()
			var det = Math.floor((new Date(this.starTime) - new Date(tstr))/(24*60*60*1000))
			// 	// console.log(tstr,det)
		    if(det>=0 && det<3){
				switch(det){
					case 0:
					this.typetext1 = '今天';break;
					case 1:
					this.typetext1 = '明天';break;
					case 2:
					this.typetext1 = '后天';break;
				}
			}else{
				var wk = new Date(this.starTime).getDay()
				this.typetext1 ='周'+this.weeks[wk];
			}
			var det2 = Math.floor((new Date(this.endTime) - new Date(tstr))/(24*60*60*1000))
			if(det2>=1 && det2<3){
				switch(det2){
					case 1:
					this.typetext2 = '明天';break;
					case 2:
					this.typetext2 = '后天';break;
				}
			} else {
				var wk = new Date(this.endTime).getDay()
				this.typetext2 ='周'+this.weeks[wk];
			}
		},
		selectDate(index){
			this.calendarShow = true
		},
		handelScroll(){
			this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
			console.log(this.scrollTop)
			if(this.scrollTop>0){
				this.layac = true
			}else{
				this.layac = false
			}
		}
	},
	watch:{
		calendarShow(val){
			if(val){
				document.documentElement.style.overflow = "hidden";

			}else{
				document.documentElement.style.overflow = "scroll";
			}
		}
	},
	created(){
		window.scrollTo(0,0)
	},
	mounted(){
		var that = this
		that.starTime = that.today().toString().split(',').join('-')
		that.endTime = that.tommorrow().toString().split(',').join('-')
		var swiper1 = new Swiper('.swiper-container1', {
	        slidesPerView: 'auto',
	        autoplay:false,
	        loop:false,
	    });
	    that.count = swiper1.slides.length
	    swiper1.on('slideChangeTransitionEnd',function(){
		    that.xfIndex = swiper1.activeIndex+1
		})
		window.addEventListener('scroll',this.handelScroll)
	}
}
</script>
